
//FLOATING PROJECTS

#animationCanvas {
  position: relative;
  overflow: hidden; /* Prevents overflow of images beyond the container */
  background-color: rgb(80, 79, 79);
}

#ContentHeader{
  position: relative;
  z-index: 10;
}

#navbar {
  position: relative;
  z-index: 10;
  text-align: center;
  display: flex;
  justify-content: center;
}

#navbar .navbar-nav {
  display: flex;
  justify-content: center;
  width: 100%;
}

#navbar .nav-item {
  display: inline-block;
  float: none;
}

.animationObject {
  position: absolute;
}

//OVERLAY EFFECT    
    .image-overlay:hover img {
      opacity: 0.5;
    }

    .image-overlay {
        position: relative;
        display: inline-block;
      }
  
      .image-overlay img {
        display: block;
        width: 100%;
      }
  
      .overlay-effect {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(172,224,249,0.2);
        opacity: 0;
        transition: opacity 0.3s ease;
      }
  
      .image-overlay:hover .overlay-effect {
        opacity: 1;
      }
